.card-list {
  margin: 0 auto;
  justify-content: flex-start;
  display: flex;
  gap: 40px;
  padding: 40px 20px 40px 150px;
  max-width: 1400px;
  flex-wrap: wrap;

  @media (max-width: 760px) {
    gap: 20px;
    padding: 20px;
    justify-content: center;
  }
}

.card {
  width: 250px;
  border: 1px solid rgb(99, 97, 97);
  background: rgb(247, 247, 207);
  background: radial-gradient(circle, rgb(247, 247, 207) 0%, rgb(164, 164, 230) 100%);
  border-radius: 5px;
  flex-shrink: 0;
  letter-spacing: 2px;
  color: white;
  transition: transform 0.3s linear, border-radius 0.3s linear;

  @media (max-width: 760px) {
  width: 200px;
  }

  &:hover {
    transform: scale(1.2);
    border-radius: 30px;
  }
  &__img {
    position: relative;
  }

  &__name {
    margin: 0;
    padding: 5px;
    text-align: center;
  }

  &__price,
  &__category {
    text-align: center;
    padding: 3px;
  }

  &__price {
    display: flex;
    justify-content: center;
    align-items: center;
    color: red;
    font-weight: bold;
    width: 100px;
    height: 100px;
    background-image: url('./Price-Tag-PNG-Image.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    right: 0;

    @media (max-width: 760px) {
      width: 85px;
      color: green;
    }
    
    @media (max-width: 446px) {
      width: 70px;
      background: transparent;
      color: red;
    }
  }

  &__available {
    text-align: center;
    display: block;
    margin: 0 auto 5px;
    background-color:#f98dca;
    color: #000;
    border-radius: 20px; 
    padding: 5px;
    height:40px; 
    width: 150px;
    font-family: inherit;
    border: 1px solid red;
    cursor: pointer;
    transition: background-color 0.3s linear;

    &:hover {
      background-color: rgb(250, 0, 0);
      border-color: #fff;;
    }
  }
}